/* eslint-disable arrow-body-style */
import React, { useContext, useState } from 'react';
import { Dropdown, Checkbox } from 'semantic-ui-react';
import firebaseApp from '../../utils/auth/firebase';
import { FirebaseContext } from '../../utils/auth/FirebaseContext';

const ProfileButton = () => {
    const [toggled, setToggled] = useState(false);
    const { currentUser } = useContext(FirebaseContext);

    const signOut = () => {
        firebaseApp.auth().signOut();
    };

    const toggle = () => setToggled((previous) => !previous);

    return (
        <Dropdown button icon="user" direction="left" className="icon">
            <Dropdown.Menu>
                <Dropdown.Item disabled>
                    <span>
                        <strong>{currentUser.email}</strong>
                        {/* <strong>{currentUser.displayName.split(' ')[0]}</strong> */}
                    </span>
                </Dropdown.Item>
                <Dropdown.Item onClick={toggle}>
                    <Checkbox
                        toggle
                        onClick={toggle}
                        checked={toggled}
                        label="Disable right-link"
                    />
                </Dropdown.Item>
                <Dropdown.Item text="Sign Out" onClick={signOut} />
            </Dropdown.Menu>
        </Dropdown>
    );
};

export default ProfileButton;
